import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { IRecordDefinition, RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import {
  IBundleDescriptor,
  IDataPageResult,
  IDefinition,
  RX_APPLICATION,
  RX_OVERLAY,
  RxBundleCacheService,
  RxCommandFactoryService,
  RxOverlayService
} from '@helix/platform/shared/api';
import { RenameDefinitionModalComponent } from '@helix/platform/shared/components';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import {
  IRowDataItem,
  IViewLayoutTemplate,
  LayoutTypes,
  RX_VIEW_DEFINITION,
  RX_VIEW_LAYOUTS,
  RxViewDefinitionDataPageService,
  RxViewDefinitionService
} from '@helix/platform/view/api';
import { IRecordGridColumn } from '@helix/platform/view/components';
import { TranslateService } from '@ngx-translate/core';
import { concat, filter as _filter, noop, some } from 'lodash';
import { BehaviorSubject, forkJoin, from, Observable, of } from 'rxjs';
import { filter, finalize, map, switchMap, tap } from 'rxjs/operators';
import { ICustomAction, IRenameAction } from '../bundle-definitions/bundle-definitions.types';
import { AX_BUNDLE_DETAILS } from '../bundle-details/bundle-details.constant';
import { IDefinitionActionConfig } from '../bundle-details/bundle-details.types';
import { RxCopyDefinitionService } from '../copy-definition/copy-definition.service';
import { DefinitionTabComponent } from '../definition-tab/definition-tab.component';
import { CopyDefinitionType } from '../copy-definition/copy-definition.types';

@Component({
  selector: 'ax-view-definition-tab',
  templateUrl: './view-definition-tab.component.html'
})
export class ViewDefinitionTabComponent implements OnInit {
  @Input()
  bundleDescriptor: IBundleDescriptor;

  @ViewChild(DefinitionTabComponent, { static: true })
  definitionTabComponent: DefinitionTabComponent;

  private rowSelectionChanged$ = new BehaviorSubject<IRowDataItem[]>([]);
  private areNewDefinitionsAllowed;

  definitionActions$ = this.rowSelectionChanged$.pipe(map((selectedRows) => this.getDefinitionActions(selectedRows)));

  definitions$: Observable<IDataPageResult>;
  gridColumns: IRecordGridColumn[] = [];
  isActionInProgress = false;
  recordDefinition: Partial<IRecordDefinition>;

  constructor(
    private rxOverlayService: RxOverlayService,
    private rxViewDefinitionDataPageService: RxViewDefinitionDataPageService,
    private rxModalService: RxModalService,
    private translateService: TranslateService,
    private rxCommandFactoryService: RxCommandFactoryService,
    private router: Router,
    private rxBundleCacheService: RxBundleCacheService,
    private rxViewDefinitionService: RxViewDefinitionService,
    private rxCopyDefinitionService: RxCopyDefinitionService
  ) {}

  private getNewViewSubAction(layout: IViewLayoutTemplate): IDefinitionActionConfig {
    return {
      id: String(layout.id),
      rxId: 'view-layout-template-' + layout.id,
      labelKey: layout.label
    };
  }

  private getDefinitionActions(selectedRows: IRowDataItem[]): IDefinitionActionConfig[] {
    const isBundleEditable = this.rxOverlayService.isBundleEditable(this.rxBundleCacheService.bundleId);

    return [
      {
        id: AX_BUNDLE_DETAILS.standardDefinitionActionNames.new,
        isDisabled: !this.areNewDefinitionsAllowed,
        subActions: concat(
          {
            groupTitle: this.translateService.instant(
              'com.bmc.arsys.rx.innovation-studio.view.layout-flexible.menu.label'
            )
          },
          _filter(RX_VIEW_LAYOUTS, { layoutType: LayoutTypes.Flexible }).map(this.getNewViewSubAction),
          {
            groupTitle: this.translateService.instant('com.bmc.arsys.rx.innovation-studio.view.layout-fixed.menu.label')
          },
          _filter(RX_VIEW_LAYOUTS, { layoutType: LayoutTypes.Fixed }).map(this.getNewViewSubAction)
        )
      },
      {
        id: AX_BUNDLE_DETAILS.standardDefinitionActionNames.rename,
        isDisabled:
          !isBundleEditable ||
          !(
            selectedRows.length === 1 &&
            this.rxOverlayService.isCustomizationEnabled('allowOverlay', selectedRows[0] as IDefinition)
          )
      },
      {
        id: AX_BUNDLE_DETAILS.standardDefinitionActionNames.revertCustomization,
        isDisabled:
          !isBundleEditable ||
          selectedRows.length === 0 ||
          some(
            selectedRows,
            (selectedRow) =>
              selectedRow.customizationPerspective !== RX_OVERLAY.customizationPerspective.customizedInThisOverlayGroup
          )
      },
      {
        id: AX_BUNDLE_DETAILS.standardDefinitionActionNames.copy,
        isDisabled: selectedRows.length !== 1 || !isBundleEditable
      },
      {
        id: AX_BUNDLE_DETAILS.standardDefinitionActionNames.delete,
        isDisabled:
          !isBundleEditable ||
          selectedRows.length === 0 ||
          some(
            selectedRows,
            (selectedRow) =>
              selectedRow.customizationPerspective !== RX_OVERLAY.customizationPerspective.createdInThisOverlayGroup
          )
      }
    ];
  }

  ngOnInit() {
    this.areNewDefinitionsAllowed = this.rxOverlayService.areNewDefinitionsAllowedSync(this.bundleDescriptor);

    this.gridColumns = [
      AX_BUNDLE_DETAILS.definitionGridColumns.name,
      AX_BUNDLE_DETAILS.definitionGridColumns.lastUpdateTime,
      AX_BUNDLE_DETAILS.definitionGridColumns.lastChangedBy,
      AX_BUNDLE_DETAILS.definitionGridColumns.scope,
      AX_BUNDLE_DETAILS.definitionGridColumns.customizationPerspective
    ];

    const fieldDefinitions = [
      {
        id: AX_BUNDLE_DETAILS.definitionGridColumns.name.fieldId,
        resourceType: RX_RECORD_DEFINITION.resourceTypes.character
      },
      {
        id: AX_BUNDLE_DETAILS.definitionGridColumns.lastUpdateTime.fieldId,
        resourceType: RX_RECORD_DEFINITION.resourceTypes.dateTime
      },
      {
        id: AX_BUNDLE_DETAILS.definitionGridColumns.lastChangedBy.fieldId,
        resourceType: RX_RECORD_DEFINITION.resourceTypes.character
      },
      {
        id: AX_BUNDLE_DETAILS.definitionGridColumns.scope.fieldId,
        resourceType: RX_RECORD_DEFINITION.resourceTypes.character
      },
      {
        id: AX_BUNDLE_DETAILS.definitionGridColumns.customizationPerspective.fieldId,
        resourceType: RX_RECORD_DEFINITION.resourceTypes.character
      }
    ];

    const fieldDefinitionsById = fieldDefinitions.reduce((result, fieldDefinition) => {
      result[fieldDefinition.id] = fieldDefinition;

      return result;
    }, {});

    this.recordDefinition = {
      fieldDefinitions,
      fieldDefinitionsById
    };

    this.definitions$ = this.rxViewDefinitionDataPageService.get({
      params: {
        propertySelection: [
          'name',
          'lastUpdateTime',
          'lastChangedBy',
          'scope',
          'customizationPerspective',
          'overlayGroupId',
          'overlayDescriptor'
        ],
        viewType: RX_VIEW_DEFINITION.types.regular,
        excludeExtensionViews: false
      }
    });
  }

  onCustomAction(event: ICustomAction): void {
    this.router.navigate([RX_APPLICATION.innovationStudioBundleId, 'view', 'new', this.rxBundleCacheService.bundleId], {
      queryParams: { layoutTemplate: event.actionId }
    });
  }

  onCopyDefinition(row: IRowDataItem): void {
    this.rxCopyDefinitionService.openCopyDefinitionComponentModal(row, CopyDefinitionType.View).catch(noop);
  }

  onDeleteDefinition(selectedRows: IRowDataItem[]): void {
    const definitionNames = selectedRows.map((definition) => definition.name);

    from(
      this.rxModalService.confirm({
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
        modalStyle: RX_MODAL.modalStyles.warning,
        message: this.definitionTabComponent.getDeleteSelectedDefinitionMessage()
      })
    )
      .pipe(
        tap((result: boolean) => {
          this.isActionInProgress = result;
        }),
        switchMap((response) =>
          response
            ? this.rxCommandFactoryService
                .forResourceType('com.bmc.arsys.rx.application.view.command.DeleteViewDefinitionsCommand')
                .execute({
                  definitionNames
                })
                .pipe(
                  tap(() => {
                    this.isActionInProgress = false;
                  }),
                  switchMap(() => this.definitionTabComponent.recordGrid.api.refresh())
                )
            : of({})
        ),
        finalize(() => {
          this.isActionInProgress = false;
        })
      )
      .subscribe();
  }

  onRenameDefinition({ selectedRow, definitionNames }: IRenameAction): void {
    const definitionType: string = this.translateService.instant('com.bmc.arsys.rx.client.definition-type.view.label');

    from(
      this.rxModalService
        .openModal({
          title: this.translateService.instant('com.bmc.arsys.rx.client.rename-definition-modal.title', {
            definitionType: definitionType.toLowerCase()
          }),
          size: 'sm',
          content: RenameDefinitionModalComponent,
          data: {
            definitionName: selectedRow.name,
            infoText: this.translateService.instant('com.bmc.arsys.rx.client.rename-definition-modal.info.message'),
            fieldLabel: this.translateService.instant(
              'com.bmc.arsys.rx.client.rename-definition-modal.definition-name.label',
              { definitionType }
            ),
            validationErrorText: this.translateService.instant(
              'com.bmc.arsys.rx.client.rename-definition-modal.name-validation.message',
              { definitionType }
            ),
            definitionType,
            definitionNames
          }
        })
        .catch(noop)
    )
      .pipe(
        filter<string>(Boolean),
        tap(() => {
          this.isActionInProgress = true;
        }),
        switchMap((newViewDefinitionName) =>
          this.rxViewDefinitionService.rename(selectedRow.name, newViewDefinitionName)
        ),
        finalize(() => {
          this.isActionInProgress = false;
        }),
        switchMap(() => this.definitionTabComponent.recordGrid.api.refresh())
      )
      .subscribe();
  }

  onRevertCustomization(selectedRows: IRowDataItem[]): void {
    from(
      this.rxModalService.confirm({
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
        modalStyle: RX_MODAL.modalStyles.warning,
        message: this.translateService.instant(
          'com.bmc.arsys.rx.innovation-studio.revert-customization.confirmation.message'
        )
      })
    )
      .pipe(
        tap(() => {
          this.isActionInProgress = true;
        }),
        switchMap((response) =>
          response
            ? forkJoin(
                selectedRows.map((definition) => this.rxViewDefinitionService.revertCustomization(definition.name))
              ).pipe(
                tap(() => {
                  this.isActionInProgress = false;
                }),
                switchMap(() => this.definitionTabComponent.recordGrid.api.refresh())
              )
            : of({})
        ),
        finalize(() => {
          this.isActionInProgress = false;
        })
      )
      .subscribe();
  }

  onRowSelectionChanged(selectedRows: IRowDataItem[]): void {
    this.rowSelectionChanged$.next(selectedRows);
  }
}
